import uniFab from '@/components/uni-fab/uni-fab.vue';
import huiziDeatil from '@/components/huizi-deatil/huizi-detail.vue';
export default {
	data() {
		return {
			title: 'Hello',
			pattern: {
				color: '#3c3e49',
				selectedColor: '#007AFF',
				backgroundColor: '#ffffff',
				buttonColor: '#EA6566'
			},
			content: [
				{
					iconPath: '/static/logo.png',
					selectedIconPath: '/static/logo.png',
					text: '首页',
					model: 'home',
					active: true
				},
				{
					iconPath: '/static/logo.png',
					selectedIconPath: '/static/logo.png',
					text: '汇总',
					model: 'calendar',
					active: false
				}
			],
			horizontal: 'right',
			vertical: 'bottom',
			direction: 'vertical',
			popMenu: true
		};
	},
	onLoad() {},
	methods: {
		trigger(e) {
			let model = e.item.model;

			switch (model) {
				case 'calendar':
					console.log('11');
					uni.navigateTo({
						url: '/pages/calendar/index',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
					break;
				default:
					console.log('butiao');
			}
		}
	},
	components: {
		uniFab,
		huiziDeatil
	}
};
